.__select {
  position: relative;
  width: 200px;
  height: 22px;
  background: #D9D9D9;
  border-radius: 5px;
}
.__select[data-state=active] .__select__content {
  opacity: 1;
}
.__select[data-state=active] .__select__label + .__select__label {
  max-height: 22px;
}
.__select[data-state=active] .select-reason-label + .select-reason-label {
  max-height: 28px;
}
.__select[data-state=active] .select-type-label + .select-type-label {
  max-height: 28px;
}
.__select .button-select {
  position: absolute;
  right: 5px;
  top: 0;
  transform: translateY(3px);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.button-select svg {
  position: absolute;
  left: 4px;
  top: 6px;
  z-index: 12;
  fill: #D9D9D9;
  filter: drop-shadow(inset 0px 4px 4px rgba(0, 0, 0, 0.5));
}

.__select__title {
  position: absolute;
  left: 4px;
  top: 2px;
  display: flex;
  align-items: center;
  width: 170px;
  height: 18px;
  padding: 0 5px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  border-radius: 5px;
  background: #fff;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.__select__title:empty {
  border-bottom: 1px solid rgba(212, 57, 30, 0.7294117647);
}

.__select__content {
  position: absolute;
  top: 100%;
  left: 4px;
  display: flex;
  flex-direction: column;
  width: calc(100% - 8px);
  background-color: #ffffff;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  overflow: hidden;
  opacity: 0;
  z-index: 8;
}
.__select__content + svg {
  position: absolute;
  top: 42%;
  right: 10px;
  pointer-events: none;
}

.__select__input {
  display: none;
}

label:has(.__select__input:checked) {
  background-color: #D9D9D9;
  pointer-events: none;
}

label:has(.__select__input:disabled) {
  opacity: 0.6;
  pointer-events: none;
}

.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 22px;
  max-height: 0;
  padding: 0 5px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;
  z-index: -2;
}
.__select__label:hover {
  background-color: #e9e9e9 !important;
}

.select-reason {
  width: 100%;
  height: 28px;
}
.select-reason-title {
  width: 100%;
  height: 28px;
  font-size: 14px;
  font-weight: 400;
  top: 0;
  left: 0;
  padding-left: 11px;
  border-radius: 10px;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.select-reason-content {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  left: 6px;
  width: calc(100% - 12px);
}
.select-reason-label {
  height: 28px;
  font-size: 14px;
  font-weight: 400;
}

.modal-decision {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 15px;
  position: relative;
}

.btn__modal-decision {
  display: block;
  cursor: pointer;
  font-family: "Comfortaa", sans-serif;
  font-weight: 400;
  font-size: 14px;
  width: 120px;
  height: 34px;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}
.btn__modal-decision:hover {
  background: rgb(243, 242, 242);
}

.modal-main p {
  font-family: "Comfortaa", sans-serif;
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 10px;
}

.input__block {
  position: relative;
}

.input__block {
  margin-bottom: 15px;
}
.input__block-input {
  padding: 0 10px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 400;
  font-size: 14px;
  width: 100%;
  height: 28px;
  background: #FFFFFF;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 10px;
}
.input__block-input:focus {
  outline: none;
}
.input__block-input:disabled {
  background: #ebebeb;
}
.input__block-input:required:invalid {
  border-bottom: 1px solid rgba(212, 57, 30, 0.7294117647);
}
.input__block-label {
  display: inline-flex;
  align-items: center;
  margin-bottom: 10px;
  position: relative;
}
.input__block-label span {
  display: inline-block;
  position: absolute;
  left: calc(100% + 15px);
  z-index: 10;
  min-width: 100%;
  width: min-content;
  padding: 10px;
  font-weight: 400;
  font-size: 12px;
  font-family: "Comfortaa", sans-serif;
  background: #fff;
  border-radius: 10px;
  border: 1px solid #cacaca;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.input__block-label p {
  margin: 0;
}
.input__block-label svg {
  margin-left: 15px;
}
.input__block-label svg:hover + span {
  opacity: 1;
}

.middle__block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  align-items: center;
  margin-bottom: 15px;
}

.middle__block .input__block {
  margin: 0;
  width: 100%;
}

.price__block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  align-items: center;
}

.input__block-results {
  display: none;
  border: 1px solid #e9e9e9;
  border-radius: 10px;
  background: #fff;
  list-style-type: none;
  position: absolute;
  top: calc(100% + 3px);
  left: 0;
  z-index: 20;
  width: 100%;
  max-height: 200px;
  overflow: overlay;
}
.input__block-results li {
  height: 25px;
  display: flex;
  align-items: center;
}
.input__block-results li:not(:last-child) {
  border-bottom: 1px solid #e9e9e9;
}
.input__block-results li:hover {
  background: #e9e9e9;
  cursor: pointer;
}
.input__block-results span {
  padding-left: 10px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 400;
  font-size: 14px;
  pointer-events: none;
}
.input__block-results::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}
.input__block-results::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #c0c0c0;
}

.select-type {
  width: 150px;
  height: 28px;
}
.select-type-title {
  width: 100%;
  height: 28px;
  font-size: 14px;
  font-weight: 400;
}
.select-type-label {
  height: 28px;
  font-size: 14px;
  font-weight: 400;
}

.name__block input + svg {
  position: absolute;
  right: 10px;
  bottom: 8px;
  cursor: pointer;
}